import React, { useState, useEffect } from 'react';
import api from '../../api/api.js';
import { useNavigate } from 'react-router-dom';
import {
  Div,
  BodyDiv,
  WrapperDiv,
  TextDivForm,
  RightDiv,
  Form,
  Input,
  TitleDiv,
  TextDiv,
  TextSpan,
  EyesImg1,
  EyesImg2,
  EyesImg3,
  Button,
  UnderDiv,
  VaildP,
  SubmitButton,
} from '../../styles/Mypage_j/ChangeInfo.styled.jsx';

import Header from '../../components/Header.jsx';
import PWOpen from '../../images/Login/PWOpen.svg';
import PWClose from '../../images/Login/PWClose.svg';

export default function ChangeInfo() {
  const [validPWState, setValidPWState] = useState(false);
  const [validNewPWState, setValidNewPWState] = useState(false);
  const [validRepeatNewPWState, setValidRepeatNewPWState] = useState(false);

  //form 
  const [form, setForm] = useState({
    nickName: '',
    email: '',
    emailCheck: '',
    password: '',
    passwordCheck: '',
    newPassword: '',
    newPasswordCheck: '',
  });

  const [validNickName, setValidNickName] = useState(null);
  const [validPW, setValidPW] = useState(null);
  const [validPWCheck, setValidPWCheck] = useState(null);
  const [data, setData] = useState({});

  const navigate = useNavigate();

  //    
  const handleSubmit = (e) => {
    e.preventDefault();
  };

  const handleMypageMainApi = async () => {
    try {
      //API  URL
      const url = `api/v1/mypage`;

      //  'jwtToken'  
      const token = getCookie('jwtToken');

      //axios.get    
      const response = await api.get(url, {
        headers: {
          'Content-Type': 'application/json',
          'ngrok-skip-browser-warning': '69420',
          Authorization: `Bearer ${token}`,
        },
      });

      console.log(response.data.result);
      setData(response.data.result);
    } catch (error) {
      console.error(
        '  api ',
        error.response ? error.response.data : error
      );
    }
  };

  //    
  const handleNewPW = (e) => {
    const newPassword = e.target.value;
    setForm({ ...form, newPassword });

    if (newPassword.trim() === '') {
      setValidPW(null);
    } else {
      const isValidPassword =
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*?&^#()])[A-Za-z\d@$!%*?&^#()]{8,12}$/.test(
          newPassword
        );
      setValidPW(isValidPassword);
    }
  };

  //     
  const hadlePWCheck = (e) => {
    const newPasswordCheck = e.target.value;
    setForm({ ...form, newPasswordCheck });

    if (newPasswordCheck.trim() === '') {
      setValidPWCheck(null);
    } else if (newPasswordCheck === form.newPassword) {
      setValidPWCheck(true);
    } else {
      setValidPWCheck(false);
    }
  };

  //    
  function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(';').shift();
    return null;
  }

  //    
  const changeinfoAPI = async () => {
    try {
      //API  URL
      const url = 'api/v1/mypage/change';

      //  'jwtToken'  
      const token = getCookie('jwtToken');

      const data = {
        nickname: form.nickName,
        passwd: form.password,
        newPasswd: form.newPassword,
        newPasswdCheck: form.newPasswordCheck,
      };

      const response = await api.put(url, data, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
      });

      const result = response.data.result;
      console.log(result);


      alert('  .');
      navigate('/Mypage');

    } catch (error) {
      console.error(
        '   ',
        error.response ? error.response.data : error
      );
      alert(error.response.data.message);
    }
  };

  const handleNickNameChange = (e) => {
    const nickName = e.target.value;
    setForm({ ...form, nickName });

    if (nickName.trim() === '') {
      setValidNickName(null);
    }
  };

  const handlePasswdChange = (e) => {
    const password = e.target.value;
    setForm({ ...form, password });
  };

  useEffect(() => {
    handleMypageMainApi();
  }, []);

  return (
    <Div>
      <BodyDiv>
        {/*  */}
        <Header></Header>

        <WrapperDiv>
          <TitleDiv>
            <TextSpan fontsize="30px" fontweight="600" color="#000000">
               
            </TextSpan>
          </TitleDiv>
          <RightDiv>
            <Form alignItems="flex-start" onSubmit={handleSubmit}>
              <TextDivForm>
                <TextDiv>
                  <TextSpan></TextSpan>
                </TextDiv>
                <Input
                  id="nickName"
                  type="text"
                  onChange={handleNickNameChange}
                  placeholder={data.nickname}
                  width="410.975px"
                  height="53.82px"
                  borderRadius="9.78px"
                />
              </TextDivForm>

              <TextDivForm>
                <TextDiv>
                  <TextSpan></TextSpan>
                </TextDiv>
                <Input
                  id="email"
                  type="email"
                  value={data.email}
                  width="410.975px"
                  height="53.82px"
                  borderRadius="9.78px"
                  readOnly
                />
              </TextDivForm>

              <TextDivForm>
                <TextDiv>
                  <TextSpan> </TextSpan>
                </TextDiv>
                <Input
                  id="currentPW"
                  type={validPWState ? 'text' : 'password'}
                  onChange={handlePasswdChange}
                  placeholder=" "
                  width="410.975px"
                  height="53.82px"
                  borderRadius="9.78px"
                />
              </TextDivForm>

              {validPWState ? (
                <EyesImg1
                  src={PWClose}
                  onClick={() => {
                    setValidPWState();
                  }}
                />
              ) : (
                <EyesImg1
                  src={PWOpen}
                  onClick={() => {
                    setValidPWState(true);
                  }}
                />
              )}

              <TextDivForm>
                <TextDiv>
                  <TextSpan> </TextSpan>
                </TextDiv>
                <Input
                  id="password"
                  type={validNewPWState ? 'text' : 'password'}
                  onChange={(e) => {
                    handleNewPW(e);
                  }}
                  placeholder=" (,  8~12)"
                  width="410.975px"
                  height="53.82px"
                  borderRadius="9.78px"
                />
                {validNewPWState ? (
                  <EyesImg2
                    src={PWClose}
                    onClick={() => {
                      setValidNewPWState();
                    }}
                  />
                ) : (
                  <EyesImg2
                    src={PWOpen}
                    onClick={() => {
                      setValidNewPWState(true);
                    }}
                  />
                )}
              </TextDivForm>
              {validPW === false ? (
                <UnderDiv>
                  <VaildP>
                     , ,   8~12
                    .
                  </VaildP>
                </UnderDiv>
              ) : null}

              <TextDivForm>
                <TextDiv>
                  <TextSpan>  </TextSpan>
                </TextDiv>
                <Input
                  id="passwordCheck"
                  type={validRepeatNewPWState ? 'text' : 'password'}
                  onChange={(e) => {
                    hadlePWCheck(e);
                  }}
                  placeholder=" "
                  width="410.975px"
                  height="53.82px"
                  borderRadius="9.78px"
                />
                {validRepeatNewPWState ? (
                  <EyesImg3
                    src={PWClose}
                    onClick={() => {
                      setValidRepeatNewPWState();
                    }}
                  />
                ) : (
                  <EyesImg3
                    src={PWOpen}
                    top={validPW === false ? '395px' : null}
                    onClick={() => {
                      setValidRepeatNewPWState(true);
                    }}
                  />
                )}
              </TextDivForm>

              {validPWCheck === false ? (
                <UnderDiv>
                  <VaildP>  .</VaildP>
                </UnderDiv>
              ) : null}

              <TextDivForm margintop="60px" justifycontent="end">
                <SubmitButton
                  value=""
                  type="submit"
                  width="410.975px"
                  height="53.82px"
                  onClick={() => {
                    changeinfoAPI();
                  }}
                />
              </TextDivForm>
            </Form>
          </RightDiv>
        </WrapperDiv>
      </BodyDiv>
    </Div>
  );
}
