import React, { useState, useEffect } from "react";
import api from "../../api/api.js";
import Header from "../../components/Header";
import {
  Div,
  BodyDiv,
  SearchDiv,
  SearchBorderDiv,
  SearchInput,
  SearchImg,
  PostNumberDiv,
  Links,
  TextSpan,
} from "../../styles/main/main-style-component.jsx";
import {
  WrapperDiv,
  WrapperBox,
  EachBox,
  CheckBox,
  NumBox,
  TitleLinks,
  TitleBox,
  DateBox,
  CheckImg,
  DeleteBt,
  DeleteDiv,
} from "../../styles/adminPage/Admin.styled.jsx";
import Pagination from "../../components/Pagination.jsx";

import Searchsvg from "../../images/MainPage/Search.svg";
import emptyCheck from "../../images/AdminPage/emptyCheck.svg";
import fullCheck from "../../images/AdminPage/fullCheck.svg";

export default function Admin() {
  const [checkStates, setCheckStates] = useState(Array(10).fill(false));
  const [Data, setData] = useState([]);
  const [paginationNum, setPaginationNum] = useState(1);
  const [totalPages, setTotalPages] = useState();
  const [searchTerm, setSearchTerm] = useState("");

  const getCheckedIds = () => {
    //   , index 1    Data  ID 
    const checkedIds = Data.filter((item, index) => checkStates[index + 1]).map(
      (item) => item.id
    );
    return checkedIds;
  };

  //     
  const handleCheckToggle = (index) => {
    const newCheckStates = [...checkStates];
    newCheckStates[index] = !newCheckStates[index];
    if (index === 0) {
      for (var i = 1; i < 11; i++) {
        newCheckStates[i] = newCheckStates[0];
      }
    }
    setCheckStates(newCheckStates);
  };

  //    
  function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
    return null;
  }

  const handleAdminPageMainApi = async (searchTerm = "") => {
    try {
      //API  URL
      const url = `api/v1/admin?page=${paginationNum}&searchTerm=${searchTerm}`;

      //  'jwtToken'  
      const token = getCookie("jwtToken");

      //axios.get    
      const response = await api.get(url, {
        headers: {
          "Content-Type": "application/json",
          "ngrok-skip-browser-warning": "69420",
          Authorization: `Bearer ${token}`,
        },
      });

      setData(response.data.items);
      setTotalPages(response.data.totalPages);
    } catch (error) {
      console.error(
        "adminPage  api ",
        error.response ? error.response.data : error
      );
    }
  };

  //   api
  const handlePublishApi = async () => {
    try {
      const checkedIds = getCheckedIds();
      //API  URL
      const url = `api/v1/admin/publish?ids=${checkedIds}`;

      //  'jwtToken'  
      const token = getCookie("jwtToken");

      //axios.get    
      const response = await api.put(
        url,
        {},
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        }
      );

      console.log(response.data);
      alert(".");
      window.location.reload();
    } catch (error) {
      console.error(
        "adminPageDetail   api ",
        error.response ? error.response.data : error
      );
    }
  };

  const handleDeleteApi = async () => {
    try {
      const checkedIds = getCheckedIds();
      //API  URL
      const url = `api/v1/admin/delete?ids=${checkedIds}`;
      //API  URL

      //  'jwtToken'  
      const token = getCookie("jwtToken");

      //axios.get    
      const response = await api.delete(url, {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
      });

      console.log(response.data);
      alert(".");
      window.location.reload();
    } catch (error) {
      console.error(
        "adminPageDetail   api ",
        error.response ? error.response.data : error
      );
    }
  };

  useEffect(() => {
    handleAdminPageMainApi();
  }, [paginationNum]);

  return (
    <Div>
      <BodyDiv>
        <Header />

        {/*   */}
        <SearchDiv>
          <SearchBorderDiv width="900px">
            <SearchInput
              type="text"
              width="800px"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              onKeyPress={(e) => {
                if (e.key === "Enter") {
                  handleAdminPageMainApi(searchTerm); //     
                }
              }}
            ></SearchInput>
            <SearchImg
              id="search"
              src={Searchsvg}
              onClick={() => handleAdminPageMainApi(searchTerm)}
            />
          </SearchBorderDiv>
        </SearchDiv>

        <DeleteDiv>
          <DeleteBt onClick={() => handleDeleteApi()}></DeleteBt>
          <DeleteBt onClick={() => handlePublishApi()}></DeleteBt>
        </DeleteDiv>

        <WrapperDiv>
          <WrapperBox>
            <EachBox>
              <CheckBox>
                <CheckImg
                  src={checkStates[0] ? fullCheck : emptyCheck}
                  onClick={() => handleCheckToggle(0)}
                />
              </CheckBox>
              <NumBox color="black" fontWeight="600">
                
              </NumBox>
              <TitleLinks>
                <TitleBox justifycontent="center"></TitleBox>
              </TitleLinks>

              <DateBox color="black" fontWeight="600">
                
              </DateBox>
              <DateBox color="black" fontWeight="600" borderLeft="0px">
                
              </DateBox>
            </EachBox>

            {Data.map((item, index) => (
              <EachBox key={item.id}>
                <CheckBox>
                  <CheckImg
                    src={checkStates[index + 1] ? fullCheck : emptyCheck}
                    onClick={() => handleCheckToggle(index + 1)}
                  />
                </CheckBox>
                <NumBox>{item.id}</NumBox>
                <TitleLinks to={`/Admin/${item.id}`}>
                  <TitleBox>{item.title}</TitleBox>
                </TitleLinks>
                <DateBox>{item.createDate}</DateBox>
                <DateBox borderLeft="0">{item.publishDate}</DateBox>
              </EachBox>
            ))}
          </WrapperBox>
        </WrapperDiv>

        {/*    */}
        {/*    */}
        <Pagination
          totalPage={totalPages}
          setPaginationNum={setPaginationNum}
          currentPage={paginationNum}
          margin={"0px"}
        />
      </BodyDiv>
    </Div>
  );
}
