import React, { useState, useEffect } from "react";
import api from '../../api/api.js'
import { useParams, useNavigate } from "react-router-dom";
import Header from "../../components/Header";
import { Div, BodyDiv } from "../../styles/main/main-style-component.jsx";
import {
  Form,
  Label,
  Input,
  Textarea,
  TextareaWrapper,
  TextareaDiv,
  Button,
  ButtonDiv,
  Links,
} from "../../styles/adminPage/AdminDetail.styled";

export default function Admin() {
  const [validPublish, setPublish] = useState(null);
  const [data, setData] = useState({
    title: "",
    link: "",
    original: "",
    fiveW1H: "",
    content: "",
    id: null,
    published: null,
  });

  const { id } = useParams();

  //    
  const handleSubmit = (e) => {
    e.preventDefault();
  };

  const handleChange = (e) => {
    const { name, value } = e.target;

    setData((prevState) => ({
      ...prevState,
      [name]: value,
    }));

    // console.log(name, value);
  };

  //    
  function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
    return null;
  }

  const handleAdminDetailApi = async () => {
    try {
      //API  URL
      const url = `api/v1/admin/${id}`;

      //  'jwtToken'  
      const token = getCookie("jwtToken");

      //axios.get    
      const response = await api.get(url, {
        headers: {
          "Content-Type": "application/json",
          "ngrok-skip-browser-warning": "69420",
          Authorization: `Bearer ${token}`,
        },
      });

      // console.log(response.data);
      setData({
        title: response.data.items[0].title,
        link: response.data.items[0].link,
        original: response.data.items[0].source,
        fiveWOneH: response.data.items[0].fiveWOneH
        ,
        content: response.data.items[0].content,
        id: response.data.items[0].id,
        published: response.data.items[0].published,
      });
    } catch (error) {
      console.error(
        "adminPage  api ",
        error.response ? error.response.data : error
      );
    }
  };

  const handlePublishApi = async () => {
    try {
      //API  URL
      const url = `api/v1/admin/publish?ids=${id}`;

      //  'jwtToken'  
      const token = getCookie("jwtToken");

      //axios.get    
      const response = await api.put(
        url,
        {},
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        }
      );

      console.log(response.data);
      alert(".");
      setPublish(true);
    } catch (error) {
      console.error(
        "adminPageDetail   api ",
        error.response ? error.response.data : error
      );
    }
  };

  const handleUnpublishApi = async () => {
    try {
      //API  URL
      const url = `api/v1/admin/unpublish/${id}`;

      //  'jwtToken'  
      const token = getCookie("jwtToken");

      //axios.get    
      const response = await api.put(
        url,
        {},
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        }
      );

      console.log(response.data);
      alert(" .");
      setPublish(false);
    } catch (error) {
      console.error(
        "adminPageDetail    api ",
        error.response ? error.response.data : error
      );
    }
  };

  const handleUpdateApi = async () => {
    try {
      //API  URL
      const url = `api/v1/admin/update`;

      //  'jwtToken'  
      const token = getCookie("jwtToken");

      //axios.get    
      const response = await api.put(
        url,
        {id: data.id,
          title: data.title,
          content: data.content
        },
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        }
      );

      console.log(response.data);
      alert(".");
      setPublish(false);
    } catch (error) {
      console.error(
        "adminPageDetail   api ",
        error.response ? error.response.data : error
      );
    }
  };

  useEffect(() => {
    handleAdminDetailApi();
  }, [validPublish]);
  return (
    <Div>
      <BodyDiv>
        <Header />

        <Form onSubmit={handleSubmit}>
          <Label for="title"></Label>
          <Input
            type="textarea"
            id="title"
            name="title"
            required
            value={data.title}
            onChange={handleChange}
          />

          <Label for="link"></Label>
          <Input
            type="textarea"
            id="link"
            name="link"
            value={data.link}
            readOnly
          />

          <TextareaWrapper>
            <TextareaDiv>
              <Label for="original"></Label>
              <Textarea
                id="original"
                name="original"
                value={data.original}
                readOnly
              ></Textarea>
            </TextareaDiv>

            <TextareaDiv>
              <Label for="fiveW1H"></Label>
              <Textarea
                id="fiveW1H"
                name="fiveW1H"
                value={data.fiveWOneH}
                readOnly
              ></Textarea>
            </TextareaDiv>

            <TextareaDiv>
              <Label for="content"></Label>
              <Textarea
                id="content"
                name="content"
                value={data.content}
                onChange={handleChange}
              ></Textarea>
            </TextareaDiv>
          </TextareaWrapper>

          <ButtonDiv>
            <div>
            <Links to={data.published ? `/detailPage/${data.id}` : "#"}>
              <Button type="button" backgroundColor={data.published ? "#588539" : null}></Button>
            </Links>
            </div>
            <div>
              <Links to="/Admin">
              <Button type="button"></Button>
            </Links>
            <Button
              type="submit"
              backgroundColor={data.published ? "#588539" : null}
              onClick={() => {
                data.published ? handleUnpublishApi() : handlePublishApi();
              }}
            >
              {data.published ? "" : ""}
            </Button>

            <Button type="submit" backgroundColor="#588539" onClick={()=>handleUpdateApi()}>
              
            </Button>
            </div>
            
          </ButtonDiv>
        </Form>
      </BodyDiv>
    </Div>
  );
}
