import React, { useState, useEffect } from 'react';
import api from '../api/api.js';

import { useParams } from 'react-router-dom';

import {
  Div,
  BodyDiv,
  Links,
  HeaderDiv,
  ExplainDiv,
  TitleSpan,
  TextSpan,
  SubscribeDiv,
  Form,
  Label,
  SubscribeInput,
  ButtonDiv,
  Button,
  SearchDiv,
  SearchBorderDiv,
  SearchInput,
  SearchImg,
  RangeDiv,
  RangeCenterDiv,
  RangeWapperDiv,
  RangeButton,
  PostAllDiv,
  BoldLine,
} from '../styles/main/main-style-component.jsx';

import Searchsvg from '../images/MainPage/Search.svg';
import Header from '../components/Header.jsx';
import NewsletterPost from '../components/NewsletterPost.jsx';
import Footer from '../components/main/Footer.jsx';
import Pagination from '../components/Pagination.jsx';

export default function MainPage(props) {
  //form 
  const [form, setForm] = useState({
    nickName: '',
    email: '',
  });

  const [validNickName, setValidNickName] = useState(null);
  const [paginationNum, setPaginationNum] = useState(1);
  const [searchString, setSearchString] = useState('');
  const [sortChoice, setSortChoice] = useState();
  const [totalPages, setTotalPages] = useState();
  const [data, setData] = useState({});

  const { id } = useParams();

  //   
  function setCookie(name, value, days) {
    let expires = '';
    if (days) {
      const date = new Date();
      date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
      expires = '; expires=' + date.toUTCString();
    }
    document.cookie = name + '=' + (value || '') + expires + '; path=/';
  }

  //    
  function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) {
      return parts.pop().split(';').shift(); //   
    }
    return null; //   null 
  }

  //    
  const handleSubmit = (e) => {
    e.preventDefault();
  };

  //   
  const subscribeAPI = async () => {
    try {
      //API  URL
      const url = 'api/v1/subscribe';

      const data = {
        email: form.email,
        nickname: form.nickName,
      };
      // console.log(form.email);
      // console.log(form.nickName);

      const response = await api.post(url, data, {
        headers: {
          'Content-Type': 'application/json',
        },
      });

      console.log(response.data.result);

      alert('  !');
    } catch (error) {
      console.error(
        '   ',
        error.response ? error.response.data : error
      );
      //  
      alert('  .');
    }
  };

  const handleEmailChange = (e) => {
    const email = e.target.value;
    setForm({ ...form, email });

    // if (email.trim() === "") {
    //   setValidEmail(null);
    // }
  };

  const handleNickNameChange = (e) => {
    const nickName = e.target.value;
    setForm({ ...form, nickName });

    if (nickName.trim() === '') {
      setValidNickName(null);
    }

    // console.log(nickName);
  };

 //    
 const newsmainPageAPI = async (pageNum, sortType) => {
  try {
    const url = `api/v1/news?page=${pageNum}&sortType=${sortType}`;
    const response = await api.get(url, {
      headers: { 'Content-Type': 'application/json', 'ngrok-skip-browser-warning': '69420' },
    });
    setData(response.data.items);
    setTotalPages(response.data.totalPages);
  } catch (error) {
    console.error('  ', error.response ? error.response.data : error);
  }
};


  //  api 
  const searchAPI = async () => {
    try {
      //API  URL
      const url = `api/v1/news?searchTerm=${searchString}`;

      //axios.get    
      const response = await api.get(url, {
        headers: {
          'Content-Type': 'application/json',
          'ngrok-skip-browser-warning': '69420',
        },
      });

      console.log(response.data.items);

      const result = response.data.items;
      setData(result);
      setTotalPages(response.data.totalPages); //    
    } catch (error) {
      console.error(
        'mainPage  ',
        error.response ? error.response.data : error
      );
    }
  };

  //  api 
  // const sortAPI = async (sortChoice) => {
  //   try {
  //     //API  URL
  //     const url = `api/v1/news?sortType=${sortChoice}`;

  //     //axios.get    
  //     const response = await api.get(url, {
  //       headers: {
  //         'Content-Type': 'application/json',
  //         'ngrok-skip-browser-warning': '69420',
  //       },
  //     });

  //     console.log(response.data.items);

  //     const result = response.data.items;
  //     setData(result);
  //     setTotalPages(response.data.totalPages); //    
  //   } catch (error) {
  //     console.error(
  //       'mainPage  ',
  //       error.response ? error.response.data : error
  //     );
  //   }
  // };

//     
const handleSortToggle = () => {
  setSortChoice((prevSort) => (prevSort === 'latest' ? 'popular' : 'latest'));
  setPaginationNum(1);  //   1 
};
  //       API 
  useEffect(() => {
    newsmainPageAPI(paginationNum, sortChoice);
  }, [paginationNum, sortChoice]);

  useEffect(() => {
    const cookieSortChoice = getCookie('sortChoice'); //   
    if (cookieSortChoice) {
      setSortChoice(cookieSortChoice); //      
    } else {
      setSortChoice('latest'); //      "" 
      setCookie('sortChoice', 'latest', 0); // ""   (7 )
    }
  }, []); //     

  useEffect(() => {
    if (sortChoice) {
      setCookie('sortChoice', sortChoice, 0); //  7   
    }
  }, [sortChoice]);

  return (
    <Div>
      <BodyDiv>
        {/*  */}
        <Header></Header>

        {/*  Header */}
        <HeaderDiv>
          <ExplainDiv>
            <TitleSpan>    </TitleSpan>
            <TextSpan textalign="right">
                  .
            </TextSpan>
            <TextSpan textalign="right">   !</TextSpan>
          </ExplainDiv>
          <SubscribeDiv>
            <Form onSubmit={handleSubmit}>
              <Label>
                <TextSpan textalign="center" fontweight="600">
                  
                </TextSpan>
                <SubscribeInput
                  type="email"
                  placeholder="1234abc@naver.com"
                  onChange={handleEmailChange}
                ></SubscribeInput>
              </Label>
              <Label>
                <TextSpan textalign="center" fontweight="600">
                  
                </TextSpan>
                <SubscribeInput
                  type="text"
                  onChange={handleNickNameChange}
                ></SubscribeInput>
              </Label>
              <ButtonDiv>
                <Button
                  value=" "
                  type="submit"
                  onClick={() => {
                    subscribeAPI();
                  }}
                />
                <Links to="/Signup">
                  <Button value=" " type="submit" />
                </Links>
              </ButtonDiv>
            </Form>
          </SubscribeDiv>
        </HeaderDiv>

        {/*   */}
        <SearchDiv>
          <SearchBorderDiv>
            <SearchInput
              type="text"
              value={searchString}
              onChange={(e) => setSearchString(e.target.value)}
              // onChange={handleInputChange} //    
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  searchAPI();
                }
              }}
            ></SearchInput>
            <SearchImg
              id="search"
              src={Searchsvg}
              onClick={() => {
                searchAPI();
              }}
            />
          </SearchBorderDiv>
        </SearchDiv>

        {/*  */}
        <RangeDiv>
          <RangeCenterDiv>
            <RangeButton
              value={sortChoice === 'latest' ? '' : ''}
              type="button"
              onClick={handleSortToggle}
              backgroundcolor="#CDDFAB"
            ></RangeButton>
          </RangeCenterDiv>
        </RangeDiv>

        {/*   */}
        <PostAllDiv>
          {data.length > 0 &&
            data.map((item, index) => (
              <Links
                to={{
                  pathname: `/detailPage/${item.id}`,
                }}
              >
                <NewsletterPost
                  key={item.id}
                  title={item.title}
                  content={item.content}
                  publishDate={item.publishDate}
                  thumbnail={item.thumbnail}
                  likeCount={item.likeCount}
                  scrapCount={item.scrapCount}
                ></NewsletterPost>
              </Links>
            ))}
        </PostAllDiv>

        {/*  */}
        <Pagination
          totalPage={totalPages}
          currentPage={paginationNum}
          setPaginationNum={setPaginationNum}
        />

        {/* footer   */}
        <BoldLine></BoldLine>

        {/* footer  */}
        <Footer></Footer>
      </BodyDiv>
    </Div>
  );
}
