import React, { useState, useEffect, useRef } from 'react';
import api from '../../api/api.js';
import {
  ChatbotDiv,
  AllMessageDiv,
  Textspan,
  MessageDiv,
  ChatbotImg,
  SendspeechbubbleDiv,
  BotspeechbubbleDiv,
  AllChattingDiv,
  ChattingDiv,
  ChattingInput,
  DotsLoader,
  Dot,
} from '../../styles/Detailpage/DetailPages.styled.jsx';
import CircleImage from '../../images/DetailPage/Circle.svg';
import MessageSendImage from '../../images/DetailPage/MessageSend.svg';

export default function DetailPage() {
  const [prompt, setPrompt] = useState('');
  const [chatHistory, setChatHistory] = useState([]);

  const [loading, setLoading] = useState(false); //   

  const allMessageDivRef = useRef(null); // AllMessageDiv    ref

  //    AllMessageDiv   
  useEffect(() => {
    if (allMessageDivRef.current) {
      allMessageDivRef.current.scrollTop =
        allMessageDivRef.current.scrollHeight;
    }
  }, [chatHistory, loading]);

  //    
  function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(';').shift();
    return null;
  }

  //    
  const handleMessageChange = (e) => {
    const message = e.target.value;
    setPrompt(message);
  };

  //  api
  const handleChatApi = async () => {
    if (!prompt.trim()) return; //      

    //     
    const newQuestion = { type: 'question', content: prompt };
    setChatHistory((prevHistory) => [...prevHistory, newQuestion]);

    setLoading(true); //   

    try {
      const url = 'api/v1/chat/send';
      const data = {
        prompt: prompt,
      };

      //  'jwtToken'  
      const token = getCookie('jwtToken');

      const response = await api.post(url, data, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
      });

      // Bot  
      const botResponse = response.data
        .reverse()
        .find((item) => item.startsWith('Bot:'));
      const botContent = botResponse
        ? botResponse.replace('Bot: ', '')
        : 'No response from bot.';

      // API   
      const newAnswer = {
        type: 'answer',
        content: botContent, // Bot   
      };
      setChatHistory((prevHistory) => [...prevHistory, newAnswer]);

      setLoading(false); //   
      setPrompt(''); //   

      console.log(response.data);
    } catch (error) {
      console.error(
        'Chatbot error',
        error.response ? error.response.data : error
      );
      setLoading(false); //      
    }
  };

  return (
    <>
      {/*   */}
      <ChatbotDiv>
        <AllMessageDiv ref={allMessageDivRef}>
          {/*    ' !'   */}
          {chatHistory.length === 0 && (
            <MessageDiv margintop="10px" flexdirection="column">
              <ChatbotImg src={CircleImage} />
              <BotspeechbubbleDiv>
                <Textspan fontsize="18px" marginbottom="0">
                   !
                </Textspan>
              </BotspeechbubbleDiv>
            </MessageDiv>
          )}

          {chatHistory.map((message, index) => (
            <>
              {message.type === 'question' ? (
                <MessageDiv key={index} justifycontent="end">
                  <SendspeechbubbleDiv height="33px">
                    <Textspan fontsize="15px" marginbottom="0">
                      {message.content}
                    </Textspan>
                  </SendspeechbubbleDiv>
                </MessageDiv>
              ) : (
                <MessageDiv key={index} margintop="10px" flexdirection="column">
                  <ChatbotImg src={CircleImage} />
                  <BotspeechbubbleDiv>
                    <Textspan fontsize="15px" marginbottom="0">
                      {message.content}
                    </Textspan>
                  </BotspeechbubbleDiv>
                </MessageDiv>
              )}
            </>
          ))}
          {/*       */}
          {loading && (
            <MessageDiv margintop="10px" flexdirection="column">
              <ChatbotImg src={CircleImage} />
              <BotspeechbubbleDiv width="102px">
                <DotsLoader>
                  <Dot />
                  <Dot />
                  <Dot />
                </DotsLoader>
              </BotspeechbubbleDiv>
            </MessageDiv>
          )}
        </AllMessageDiv>

        {/*   */}
        <AllChattingDiv>
          <ChattingDiv>
            <ChattingInput
              type="text"
              placeholder=" ."
              value={prompt}
              onChange={handleMessageChange}
              //    
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  handleChatApi();
                  setPrompt(''); //  
                }
              }}
            ></ChattingInput>
            <ChatbotImg
              width="27px"
              height="23px"
              src={MessageSendImage}
              cursor="pointer"
              onClick={() => {
                handleChatApi();
                setPrompt('');
              }}
            />
          </ChattingDiv>
        </AllChattingDiv>
      </ChatbotDiv>
    </>
  );
}
