import React from 'react';
import {
  NewsDivChat,
  AllMessageDiv,
  Textspan,
  MessageDiv,
  ChatbotImg,
  SendspeechbubbleDiv,
  BotspeechbubbleDiv,
  AllChattingDiv,
  ChattingDiv,
  ChattingInput,
  NoLoginChatDiv,
  NoticeDiv,
} from '../../styles/Detailpage/DetailPages.styled.jsx';

import CircleImage from '../../images/DetailPage/Circle.svg';
import MessageSendImage from '../../images/DetailPage/MessageSend.svg';

export default function NoLoginDetailPage() {
  return (
    <>
      {/*  &  */}
      {/*   -    */}
      <NoLoginChatDiv>
        <NoticeDiv>
          <Textspan fontweight="600" textalign="center" marginbottom="0">
               !
          </Textspan>
        </NoticeDiv>
        <AllMessageDiv>
          <MessageDiv justifycontent="end">
            <SendspeechbubbleDiv height="33px">
              <Textspan fontsize="15px" marginbottom="0">
                 ?
              </Textspan>
            </SendspeechbubbleDiv>
          </MessageDiv>
          <MessageDiv height="auto" margintop="10px" flexdirection="column">
            <ChatbotImg src={CircleImage} />
            <BotspeechbubbleDiv>
              <Textspan fontsize="15px" marginbottom="0">
                       . 
                     , 2 3
                     . 
                          ,
                        
                 .        
                .
              </Textspan>
            </BotspeechbubbleDiv>
          </MessageDiv>

          <MessageDiv justifycontent="end">
            <SendspeechbubbleDiv height="33px">
              <Textspan fontsize="15px" marginbottom="0">
                 5    ?
              </Textspan>
            </SendspeechbubbleDiv>
          </MessageDiv>
          <MessageDiv height="auto" margintop="10px" flexdirection="column">
            <ChatbotImg src={CircleImage} />
            <BotspeechbubbleDiv>
              <Textspan fontsize="15px" marginbottom="0">
                  .
              </Textspan>
            </BotspeechbubbleDiv>
          </MessageDiv>
        </AllMessageDiv>

        {/*   */}
        <AllChattingDiv>
          <ChattingDiv>
            <ChattingInput
              type="text"
              placeholder=" ."
            ></ChattingInput>
            <ChatbotImg
              width="27px"
              height="23px"
              src={MessageSendImage}
              cursor="pointer"
            />
          </ChattingDiv>
        </AllChattingDiv>
      </NoLoginChatDiv>
    </>
  );
}
