import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import api from '../../api/api.js'
import {
  BodyDiv,
  WrapperDiv,
  LogoImg,
  Form,
  Input,
  Submit,
  UnderDiv,
  Button,
  VaildP,
} from "../../styles/Login/LoginSignUp.styled";

import Logo from "../../images/Logo.svg";

export default function SignUp() {
  const navigate = useNavigate();

  //form 
  const [form, setForm] = useState({
    nickName: "",
    email: "",
    emailVerifyCode: "",
    password: "",
    passwordCheck: "",
  });

  const [validEmailVerification, setValidEmailVerification] = useState(null); //  
  const [validEmailDuplicate, setValidEmailDuplicate] = useState(null); //    
  const [validEmailCheck, setValidEmailCheck] = useState(null); //  
  const [validEmail, setValidEmail] = useState(null); //  
  const [validPW, setValidPW] = useState(null);
  const [validPWCheck, setValidPWCheck] = useState(null);
  const [validNickName, setValidNickName] = useState(null);

  //   
  const handlePW = (e) => {
    const password = e.target.value;
    setForm({ ...form, password });

    if (password.trim() === "") {
      setValidPW(null);
    } else {
      const isValidPassword =
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*?&^#()])[A-Za-z\d@$!%*?&^#()]{8,12}$/.test(
          password
        );
      setValidPW(isValidPassword);
    }
  };

  //    
  const hadlePWCheck = (e) => {
    const passwordCheck = e.target.value;
    setForm((prevForm) => ({ ...prevForm, passwordCheck }));

    if (passwordCheck.trim() === "") {
      setValidPWCheck(null);
    } else if (passwordCheck === form.password) {
      setValidPWCheck(true);
    } else {
      setValidPWCheck(false);
    }
  };

  // form input    ?
  const handleEmailChange = (e) => {
    const email = e.target.value;
    setForm({ ...form, email });
    setValidEmail(true);

    if (email.trim() === "") {
      setValidEmail(null);
    }
  };

  const handleEmailVerifyCode = (e) => {
    const emailVerifyCode = e.target.value;
    setForm({ ...form, emailVerifyCode });

    if (emailVerifyCode.trim() === "") {
      setValidEmailVerification(null);
    }
  };

  const handleNickNameChange = (e) => {
    const nickName = e.target.value;
    setForm({ ...form, nickName });
    setValidNickName(true);

    if (nickName.trim() === "") {
      setValidNickName(null);
    }
  };

  //    
  const handleSubmit = (e) => {
    e.preventDefault();
  };

  //    
  const isEmailDuplicateAPI = async () => {
    try {
      //API  URL
      const url =
        "api/v1/duplicateCheck";

      const data = {
        email: form.email,
      };

      const response = await api.post(url, data, {
        headers: {
          "Content-Type": "application/json",
        },
      });

      setValidEmailDuplicate(true);
      console.log(response.data.result);

      // alert("  .");
    } catch (error) {
      console.error(
        "   ",
        error.response ? error.response.data : error
      );
      setValidEmailDuplicate(false);
    }
  };

  //    
  const requestEmailVerificationAPI = async () => {
    try {
      //API  URL
      const url =
        "api/v1/email/send";

      const data = {
        email: form.email,
      };

      const response = await api.post(url, data, {
        headers: {
          "Content-Type": "application/json",
        },
      });

      const result = response.data.result;
      console.log(result);
      setValidEmailVerification(true);

      alert(" .");
    } catch (error) {
      console.error(
        "  ",
        error.response ? error.response.data : error
      );
      setValidEmailVerification(false);
    }
  };

  //    
  const confirmEmailVerificationAPI = async () => {
    try {
      //API  URL
      const url =
        "api/v1/email/verify";

      const data = {
        email: form.email,
        verifyCode: form.emailVerifyCode,
      };

      const response = await api.post(url, data, {
        headers: {
          "Content-Type": "application/json",
        },
      });

      const result = response.data.result;
      console.log(result);
      setValidEmailCheck(true);

      alert(" .");
    } catch (error) {
      console.error(
        "  ",
        error.response ? error.response.data : error
      );
      setValidEmailCheck(false);
      alert("  .");
    }
  };

  //  
  const signUpAPI = async () => {
    try {
      //API  URL
      const url = "api/v1/join";

      const data = {
        email: form.email,
        passwd: form.password,
        passwdCheck: form.passwordCheck,
        nickname: form.nickName,
      };

      const response = await api.post(url, data, {
        headers: {
          "Content-Type": "application/json",
        },
      });

      const result = response.data.result;
      console.log(result);

      alert(" !");
      navigate("/Login");
    } catch (error) {
      console.error(
        " ",
        error.response ? error.response.data : error
      );
      alert(error.response.data.result);
    }
  };

  return (
    <BodyDiv>
      <WrapperDiv>
        <LogoImg src={Logo} marginBottom="20px" />

        <Form onSubmit={handleSubmit}>
          <Input
            id="nickName"
            type="text"
            placeholder=""
            width="410.975px"
            height="53.82px"
            borderRadius="9.78px"
            onChange={handleNickNameChange}
          />

          <Input
            id="email"
            type="email"
            placeholder=""
            width="410.975px"
            height="53.82px"
            borderRadius="9.78px"
            onChange={handleEmailChange}
          />
          <UnderDiv justifyContent={validEmailDuplicate === null ? "end" : "space-between"}>
            {validEmailDuplicate === null ? null : validEmailDuplicate ? (
              <VaildP color="#588539" >
                  .
              </VaildP>
            ) : (
              <VaildP >  .</VaildP>
            )}

            {validEmailDuplicate ? (
              <Button
                backgroundColor="#588539"
                color="#FFFFFF"
                borderColor="#588539"
              >
                 
              </Button>
            ) : (
              <Button
                onClick={() => {
                  isEmailDuplicateAPI();
                }}
              >
                 
              </Button>
            )}

            {validEmailVerification ? (
              <Button
                backgroundColor="#588539"
                color="#FFFFFF"
                borderColor="#588539"
                onClick={() => {
                  requestEmailVerificationAPI();
                }}
              >
                
              </Button>
            ) : (
              <Button
                onClick={() => {
                  requestEmailVerificationAPI();
                }}
              >
                 
              </Button>
            )}
          </UnderDiv>

          <Input
            id="emailVerifyCode"
            type="text"
            placeholder=" "
            width="410.975px"
            height="53.82px"
            borderRadius="9.78px"
            onChange={handleEmailVerifyCode}
          />

          {/* <UnderDiv justifyContent="space-between"> */}
          <UnderDiv justifyContent={validEmailCheck === null ? "end" : "space-between"}>
            {validEmailCheck === null ? null : validEmailCheck ? (
              <VaildP color="#588539">.</VaildP>
            ) : (
              <VaildP>  .</VaildP>
            )}

            {validEmailCheck ? (
              <Button
                backgroundColor="#588539"
                color="#FFFFFF"
                borderColor="#588539"
              >
                 
              </Button>
            ) : (
              <Button
                onClick={() => {
                  confirmEmailVerificationAPI();
                }}
              >
                 
              </Button>
            )}
          </UnderDiv>

          <Input
            id="password"
            type="password"
            onChange={handlePW}
            placeholder=" (,  8~12)"
            width="410.975px"
            height="53.82px"
            borderRadius="9.78px"
          />

          {validPW === false ? (
            <UnderDiv>
              <VaildP>
                 , ,   8~12 .
              </VaildP>
            </UnderDiv>
          ) : null}

          <Input
            id="passwordCheck"
            type="password"
            onChange={hadlePWCheck}
            placeholder=" "
            width="410.975px"
            height="53.82px"
            borderRadius="9.78px"
          />

          {validPWCheck === false ? (
            <UnderDiv>
              <VaildP>  .</VaildP>
            </UnderDiv>
          ) : null}

          {validNickName &&
          validEmailCheck &&
          validEmailDuplicate &&
          validEmailVerification &&
          validPW &&
          validPWCheck ? (
            <Submit
              value=""
              type="submit"
              width="410.975px"
              height="53.82px"
              marginTop="60px"
              marginBottom="30px"
              onClick={() => {
                signUpAPI();
              }}
            />
          ) : (
            <Submit
              value=""
              type="submit"
              width="410.975px"
              height="53.82px"
              marginTop="60px"
              marginBottom="30px"
              backgroundColor="#C0C0C0"
            />
          )}
        </Form>
      </WrapperDiv>
    </BodyDiv>
  );
}
